import * as React from 'react';
import styled from 'styled-components';
import { isNil } from 'lodash-es';
import { theme } from '@ali/we-design';

interface CommonPanelProps {
  placeholder?: React.ReactNode;
  style?: React.CSSProperties;
}

const CommonPanelWrapper = styled.div`
  display: flex;
  height: 100%;
  width: 100%;
  overflow: auto;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 12px;
  text-align: center;
  color: ${() => theme.colorText4};
`;

const Placeholder = styled.div`
  padding: 15px;
`;

const CommonPanel: React.FC<CommonPanelProps> = props => {
  const { children, placeholder, style = {} } = props;
  return (
    <CommonPanelWrapper style={style}>
      {!isNil(placeholder) && <Placeholder>{placeholder}</Placeholder>}
      {children}
    </CommonPanelWrapper>
  );
};

export default CommonPanel;
